.pl {
    padding: 50px 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.pl-text{
    width: 65%;
}

.pl-title{
    font-size: 50px;
    font-weight: 600;
}

.pl-desc {
    margin: 20px 0px;
}

.pl-list{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.projectpic {
    text-align: center;
 }
 
 .projectpic img {
     display: inline-block;
     margin:5px;
     width: 35%;
     /* height:200px; */
     margin: 10px;
 }

 @media screen and (max-width:480px) {
    .pl{
        padding: 10px;
    }

    .pl-text {
        width: 100%;
    }

    .pl-desc {
        display: none;
    }

    
 }
