.a {
    height: 100vh;
    display: flex;
    align-items: center;
}

.a-left {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    height: 100%;
}

.a-right{
    flex:1;
}

.a-card.bg{
    position: absolute;
    top: 50px;
    left: 50px;
    background-color: black;

}

.a-card {
    width: 60%;
    height: 70vh;
    border-radius: 30px;
    position: relative;
    overflow: hidden;
}

.a-img{
    width: 100%;
    height:100%;
    object-fit: cover;
}

.a-badge-img{
    width:200px;
    height:200px;
}

.a-title{
    font-weight: bold;
}

.a-sub{
    margin: 20px 0px;
}

.a-desc{
    font-weight: heavy;
    font-size: 120%;
}

.a-badge{
    margin-top: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.a-badge-text{
    width:70%;
}

.a-badge-text{
    font-weight: bold;
    color: goldenrod;
    margin-bottom:10px ;
}

@media screen and (max-width:480px) {
    .a {
        flex-direction: column;
        text-align: center;
        margin-top: 50px;
    }

    .a-left{
        width:100%
    }

    .a-card {
        height: 30vh;
    }

    .a-card.bg, .a-badge {
        display: none;
    }

    .a-right{
        padding: 20px;
    }
}